import React, { memo, NamedExoticComponent, FunctionComponent } from 'react';
import { Tooltip, LineChart, Line, ResponsiveContainer } from 'recharts';
import '../index.less';
interface IData {
  date: string;
  value: number | string;
}
interface IMiniLine {
  data: IData[];
}
interface ICustomToolTip {
  active: boolean;
  payload: any;
}

const CustomTooltip: FunctionComponent<ICustomToolTip> = ({ active, payload }) => {
  if (active) {
    return (
      <div className='custom-tooltip'>
        <p className='label'>{`${payload[0].payload.date} : ${payload[0].value}`}</p>
      </div>
    );
  }

  return null;
};
export const MiniLine: NamedExoticComponent<IMiniLine> = memo(({ data }) => {
  return (
    <div className={'miniChart'}>
      <div className={'chartContent'}>
        <ResponsiveContainer>
          <LineChart data={data}>
            <Line type='monotone' dataKey='value' stroke='#8884d8' strokeWidth={2} />
            //@ts-ignore
            <Tooltip content={props => <CustomTooltip {...props} />} />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
});
